.layout {
    display: flex;
    height: 100vh;
}

.sidebar {
    height: 100%;
    width: 18rem;
    background-color: #f7f7f7;
    border-right: solid 1px #e3e3e3;
    display: flex;
    flex-direction: column;
}

.sidebar > * {
    padding-left: 2rem;
    padding-right: 2rem;
}

.sidebar nav {
    flex: 1;
    overflow: auto;
    padding-top: 1rem;
}

.sidebar nav a span {
    float: right;
    color: #eeb004;
}
.sidebar nav a.active span {
    color: inherit;
}

i {
    color: #818181;
}
.sidebar nav .active i {
    color: inherit;
}

.sidebar ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebar li {
    margin: 0.25rem 0;
}

.sidebar li:first-child {
    margin-top: 0;
}

.sidebar nav a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;

    white-space: pre;
    padding: 0.5rem;
    border-radius: 8px;
    color: inherit;
    text-decoration: none;
    gap: 1rem;
}

.sidebar nav a:hover {
    background: #e3e3e3;
}

.sidebar nav a.active {
    background: #d4e9fc;
    /*color: white;*/
}

.main {
    flex-grow: 1;
    height: 100%;
    position: relative;
    padding: 1rem;
}

.active {
    text-decoration: underline;
}

.non-active {
    text-decoration: none;
}