.user-results,
.player-results {
    display: flex;
    flex-direction: row;
}

.players-results {
    margin-top: 1rem;
}

.user-results .user-name,
.player-results .player-name {
    width: 7rem;
    background: #d4e9fc;
    display: flex;
    align-items: center;
    padding: 0.25rem 0.5rem;
    box-sizing: border-box;
}

.user-results .user-name:hover {
    cursor: pointer;
}

.user-results .user-name.selected {
    text-decoration: underline;
}

.player-results .player-name {
    width: 10rem;
}

.tour-by-tour .points {
    padding: 2px;
    margin-left: 0.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #e3e3e3;
    width: 1.25rem;
}